.header {
  width: 100%;
  height: 50px;
  position: fixed;
  top: 0;
  background-color: rgb(0, 0, 0);
}
.fav {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin-left: 5%;
  background-color: rgba(0, 0, 0, 0.568);
  justify-content: center;
  margin-top: 50px;
}
.fav .fav-movies {
  width: 20%;
  width: 200px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.fav .fav-movies img {
  height: 300px;
}

.header > p {
  width: 100%;
  position: fixed;
  top: 10px;
  color: aquamarine;
  margin-left: 90vh;
  font-size: 4vh;
  text-decoration: blanchedalmond;
}
